<template>
  <div id="app">
    <h1>我的博客</h1>
    <comment-form @add-comment="doAdd" />
    <comment-list title="热门评论" :xcomments="comments" @delete-comment="doDelete" />
  </div>
</template>

<script>
 import CommentForm from './components/comment-form.vue';
 import CommentList from './components/comment-list.vue';
 import axios from 'axios';

 export default {
   name: 'App',
   data() {
     return  {
       comments: [

       ]
     }
   },
   methods: {
        doDelete(id) {
            axios.get("http://localhost:8080/_war_exploded/comment/sc?id="+id)
              .then(resp =>{
                this.load();
            })
        },
        doAdd(data) {
            var rsp = new URLSearchParams();
            rsp.append("name",data.name);
            rsp.append("body",data.body);
            axios({
                url:"http://localhost:8080/_war_exploded/comment/add",
                method:"post",
                data:rsp
            }).then(resp =>{
                this.load();
            })
        },
        load:function(){
            axios({
                url: "http://localhost:8080/_war_exploded/comment/list",
                responseType: "json"
            }).then(resp =>{
                this.comments = resp.data;
            }).catch(err => {
              alert(err)
            })
        }
   },
   created:function () {
     this.load();
   },
   components: {
     CommentForm, CommentList
   }
 }
</script>

<style>
 #app {
   font-family: Avenir, Helvetica, Arial, sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   text-align: center;
   color: #2c3e50;
   margin-top: 60px;
 }
