<template>
  <section class="comment-form">
    <h3>{{formTitle}}</h3>
    <div>
      <el-input v-model="name" placeholder="请输入名称"></el-input>
      <el-input v-model="body" placeholder="请输入内容"></el-input>
      <el-button type="primary" plain v-on:click="addComment">主要按钮</el-button>
    </div>
  </section>
</template>

<script>
 export default {
   props: ["formTitle"],
   data: function () {
     return {
       name: "",
       body: ""
     };
   },
   methods: {
     addComment: function() {
       this.$emit("add-comment", {
         name: this.name,
         body: this.body,
       });
       this.name = this.body = "";
     }
   }
 };
</script>

<style scoped>
 .comment-form {
   width: 400px;
   margin: 0 auto;
 }
 .comment-form > div {
   display: flex;
   flex-flow: column;
 }
 el-input {
   margin-bottom: 1em;
   padding: 10px;
 }

</style>